@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&family=Source+Sans+Pro:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.player-card{border-radius:20px;overflow:hidden;box-shadow:0 0 20px rgba(0,0,0,0.35);position:relative;z-index:10}.player-card__image{background-color:#403550;position:relative;height:300px;display:flex;align-items:flex-end}.player-card__image img{display:block;margin:0 auto;max-width:100%;max-height:100%}.player-card__image .overlay{display:block;position:absolute;width:100%;height:100%;background-image:url("https://img.freepik.com/free-vector/festive-blurred-lights_53876-89102.jpg");background-size:cover;background-position:center;background-repeat:no-repeat;opacity:0.15;background-blend-mode:multiply}.player-card__image.has-error img{align-self:center;opacity:0.5}.player-card__image.has-error .overlay{display:none}.player-card__info{background-image:url("https://images.freecreatives.com/wp-content/uploads/2016/02/10-Free-Carbon-Fiber-Photoshop-Patterns.jpg");color:white;padding:10px 15px;text-transform:uppercase;background-size:150% auto;font-size:1.2em;font-weight:300}.player-card__info strong{display:block;margin-bottom:5px;font-family:"Source Sans Pro", sans-serif;font-weight:inherit}.player-card__info p{margin:0;font-size:65%}.player-statistics{background-color:#8e36f3;color:white;display:grid;grid-template-columns:repeat(3, 1fr);padding:25px 15px}.player-statistics__block{text-align:center;padding:5px 15px;text-transform:uppercase}.player-statistics__block:not(:nth-child(3n)){border-right:1px solid white}.player-statistics__block strong{display:block;font-size:1.875em}.player-statistics__block span{font-size:12px}

.players-container{width:1140px;max-width:100%;margin:0 auto;padding:0 30px}.players-container__wrapper{display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr));align-items:center;justify-content:center;grid-gap:30px;gap:30px}

*,*::before,*::after{box-sizing:border-box}body{background:#2e1044;min-height:100vh;font-family:"Lato", sans-serif}body::after{content:"";position:fixed;display:block;width:100%;height:100%;top:0;left:0;z-index:-1;background:linear-gradient(350deg, #2e1044 0%, #6b2ab9 100%) no-repeat;font-size:14px;font-weight:normal}.App{padding:60px 0}.nba-logo{position:fixed;height:85%;mix-blend-mode:multiply;opacity:0.15;right:0;bottom:0;transform:translate(35%, 15%);z-index:0;max-width:50%}.nba-logo img{width:100%;height:100%;object-fit:cover;object-position:top left}.head-text{text-transform:uppercase;color:white;font-weight:400;opacity:0.85;font-size:1.5em;font-family:"Source Sans Pro", sans-serif}@media (max-width: 767px){.head-text{text-align:center}}

